
"use client";

import * as React from "react";
import { PageHeader } from "@/components/page-header";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { ItemsTable } from "@/components/dashboard/items-table";
import type { Item, Category } from "@/types";
import { Skeleton } from "@/components/ui/skeleton";

export default function ItemsPage() {
  const [items, setItems] = React.useState<Item[]>([]);
  const [categories, setCategories] = React.useState<Category[]>([]);
  const [isClient, setIsClient] = React.useState(false);

  React.useEffect(() => {
    setIsClient(true);
    try {
      const storedItems = localStorage.getItem("inventory-manager-items");
      if (storedItems) {
        setItems(JSON.parse(storedItems));
      }
      const storedCategories = localStorage.getItem("inventory-manager-categories");
      if (storedCategories) {
        setCategories(JSON.parse(storedCategories));
      }
    } catch (error) {
      console.error("Failed to parse from localStorage", error);
    }
  }, []);

  React.useEffect(() => {
    if(isClient) {
        localStorage.setItem("inventory-manager-items", JSON.stringify(items));
    }
  }, [items, isClient]);

  const handleAddItem = (newItem: Omit<Item, "id" | "averageDailySales" | "leadTimeDays">) => {
    const id = `item-${Date.now()}`;
    const averageDailySales = Math.floor(Math.random() * 20) + 1;
    const leadTimeDays = Math.floor(Math.random() * 10) + 2;

    setItems((prevItems) => [
      ...prevItems,
      {
        ...newItem,
        id,
        averageDailySales,
        leadTimeDays,
      },
    ]);
  };

  const handleEditItem = (updatedItem: Item) => {
    setItems((prevItems) =>
      prevItems.map((item) =>
        item.id === updatedItem.id ? updatedItem : item
      )
    );
  };

  const handleDeleteItem = (itemId: string) => {
    const itemToDelete = items.find(i => i.id === itemId);
    setItems((prevItems) => prevItems.filter((item) => item.id !== itemId));
    
    if(itemToDelete) {
        setCategories((prevCategories) => {
            const updatedCategories = prevCategories.map(c => {
                if(c.id === itemToDelete.categoryId) {
                    return {...c, itemCount: c.itemCount > 0 ? c.itemCount -1 : 0}
                }
                return c;
            });
            if (isClient) {
              localStorage.setItem('inventory-manager-categories', JSON.stringify(updatedCategories));
            }
            return updatedCategories;
        });
    }
  };

  React.useEffect(() => {
    if (!isClient) return;
  
    const itemCounts = items.reduce((acc, item) => {
      if (item.categoryId) {
        acc[item.categoryId] = (acc[item.categoryId] || 0) + 1;
      }
      return acc;
    }, {} as Record<string, number>);
  
    const categoriesWithCounts = categories.map(c => ({
      ...c,
      itemCount: itemCounts[c.id] || 0,
    }));

    if (JSON.stringify(categoriesWithCounts) !== JSON.stringify(categories)) {
        setCategories(categoriesWithCounts);
        localStorage.setItem('inventory-manager-categories', JSON.stringify(categoriesWithCounts));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [items, isClient]);


  const lowStockItems = items.filter(
    (item) => item.quantity === 0
  );
  
  if (!isClient) {
    return (
      <>
        <PageHeader
            title="Items"
            description="Manage your products and inventory."
        />
        <div className="space-y-4">
            <Skeleton className="h-10 w-48" />
            <div className="flex items-center py-4">
                <Skeleton className="h-10 w-full max-w-sm" />
                <Skeleton className="ml-auto h-10 w-28" />
            </div>
            <Skeleton className="h-96 w-full rounded-md border" />
        </div>
      </>
    );
  }

  return (
    <>
      <PageHeader
        title="Items"
        description="Manage your products and inventory."
      />
      <Tabs defaultValue="all">
        <TabsList>
          <TabsTrigger value="all">All Items</TabsTrigger>
          <TabsTrigger value="low-stock">Low Stock</TabsTrigger>
        </TabsList>
        <TabsContent value="all">
          <ItemsTable
            items={items}
            categories={categories}
            onAddItem={handleAddItem}
            onEditItem={handleEditItem}
            onDeleteItem={handleDeleteItem}
          />
        </TabsContent>
        <TabsContent value="low-stock">
          <ItemsTable
            items={lowStockItems}
            categories={categories}
            onAddItem={handleAddItem}
            onEditItem={handleEditItem}
            onDeleteItem={handleDeleteItem}
          />
        </TabsContent>
      </Tabs>
    </>
  );
}
